.element {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  background-color: var(--main-color-background);
}

.technologies {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.pageTitle {
  grid-column: span 2 / auto;
  text-align: left;
}

.elementDescription {
  display: flex;
  position: relative;
  flex-direction: column;
  width: auto;
  flex: 1 1 auto;
  padding: 24px;
  order: 2;
}

.elementTitle {
  color: rgb(41, 47, 53);
  font-size: 26px;
  font-weight: 800;
  letter-spacing: 0px;
  line-height: 48px;
  text-align: left;
}

.elementText {
  color: rgb(41, 47, 53);
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 24px;
  text-align: left;
}

.elementTechnology {
  color: var(--main-color-background);
  background-color: rgb(41, 47, 53);
  margin: 2px;
  padding: 5px;
  border-radius: 4px;
}

.elementLinks {
  display: flex;
}

.elementsButton {
  background-color: rgb(240, 240, 240);
  position: relative;
  height: 44px;
  text-decoration: none;
  padding: 0px 12px;
  border-radius: 4px;
  cursor: pointer;
  color: rgb(41, 47, 53);
  vertical-align: middle;
  display: inline-flex;
  width: fit-content;
  align-items: center;
  font-weight: 500;
  margin-top: 24px;
  margin-right: 10px;
}

.image {
  width: 100%;
  position: relative;
  background-color: rgb(41, 47, 53);
  order: 1;
}

img {
  display: block;
  height: 100%;
  width: 100%;
  max-width: 100%;
}

@media (min-width: 1300px) {
  .element {
    display: flex;
    grid-column: span 2 / auto;
    flex-direction: row;
  }

  .description {
    justify-content: center;
    width: 50%;
  }

  .image {
    width: 50%;
  }

  .elementDescription {
    width: 50%;
    margin-bottom: 80px;
  }

  .reverseImage {
    order: 2;
  }

  .reverseDescription {
    order: 1;
  }
}

@media (max-width: 720px) {
  .element {
    margin-top: 24px;
    padding-bottom: 20px;
  }

  .elementDescription {
    width: fit-content;
  }
}
